export default {
	data() {
		return {
			worksId:'',
			worksData:{},
		}
	},
	onLoad(options) {
		console.log(options);
		this.worksId = parseInt(options.worksId);
		console.log(this.worksId);
		this.getWorksAndUserById(this.worksId);
	},
	methods: {
		getWorksAndUserById:function(worksId){
			uni.request({
				url: this.createApiUrl('works/get/works'),
				method: 'GET',
				data: {
					worksId:worksId
				},
				success: res => {
					this.worksData = res.data.data;
					console.log(res);
				},
				fail: () => {},
				complete: () => {}
			});
		}
	}
}
